const ACTIVE_MENU_ITEM_CLASS = "triMenuItemActive"
const ACTIVE_TOP_PARENT_MENU_ITEM_CLASS = "triMenuItemTopParentActive";
const ACTIVE_MENU_ITEM_BACKGROUND_CLASS = "triMenuItemActiveBackground";
const ACTIVE_TOP_PARENT_MENU_ITEM_BACKGROUND_CLASS = "triMenuItemTopParentActiveBackground"
const ACTIVE_TOP_PARENT_DESCENDANT_BACKGROUND_CLASS = "triMenuItemTopParentActiveDescendantBackground"

export class ActiveMenuItemHandler {

    constructor(hierarchyListItemHandler) {
        this.hierarchyListItemHandler = hierarchyListItemHandler;
        this.currentActiveMenuId = null;
        this.currentActiveTopParentMenuId = null
    }

    handle(menuId) {
        //First remove menu active classes
        this._removeActiveMenuItemClasses();
        
        var triMenuItemElement;
        var triMenuItemTopParentElement;
        
        //Add active to selected menu and top parent
        let menuItem = this.hierarchyListItemHandler.handleValidMenuItem(menuId);
        this.currentActiveMenuId = menuItem.id;
        triMenuItemElement = document.getElementById(this.currentActiveMenuId);
        if (triMenuItemElement.classList.contains("sideNavMenuItemWithChildren")) {
            triMenuItemElement.parentElement.parentElement.parentElement.classList.add(ACTIVE_MENU_ITEM_BACKGROUND_CLASS);
            triMenuItemElement.parentElement.parentElement.classList.add(ACTIVE_MENU_ITEM_BACKGROUND_CLASS);
        } else {
            triMenuItemElement.classList.add(ACTIVE_MENU_ITEM_BACKGROUND_CLASS);
        }
        triMenuItemElement.classList.add(ACTIVE_MENU_ITEM_CLASS);
            
        this.currentActiveTopParentMenuId = menuItem.topParentMenuId;
       
        if (this.currentActiveTopParentMenuId != null) {
            triMenuItemTopParentElement = document.getElementById(this.currentActiveTopParentMenuId);
            if (triMenuItemTopParentElement.classList.contains("sideNavMenuItemWithChildren")) {
                triMenuItemTopParentElement.parentElement.parentElement.parentElement.classList.add(ACTIVE_TOP_PARENT_MENU_ITEM_BACKGROUND_CLASS);
            }
            triMenuItemTopParentElement.classList.add(ACTIVE_TOP_PARENT_MENU_ITEM_CLASS);
        }

        //Add top level descendant classes
        let topLevelMenuItemId = this.currentActiveTopParentMenuId != null ? this.currentActiveTopParentMenuId: this.currentActiveMenuId;
        let childrenMenuItemIds = this.hierarchyListItemHandler.getChildrenListItemIdsByTopParentId(topLevelMenuItemId);
        //Iterate through each child
        if (childrenMenuItemIds != null) {
            for (var i = 0; i < childrenMenuItemIds.length; i++) {
                let childMenuItemElement = document.getElementById(childrenMenuItemIds[i]);
                if (childMenuItemElement != null) {
                    if (childMenuItemElement.classList.contains("sideNavMenuItemWithChildren")) {
                        childMenuItemElement.parentElement.parentElement.parentElement.classList.add(ACTIVE_TOP_PARENT_DESCENDANT_BACKGROUND_CLASS);
                    } else {
                        childMenuItemElement.classList.add(ACTIVE_TOP_PARENT_DESCENDANT_BACKGROUND_CLASS);
                    }
                }
            }
        }   
    } 

    //First remove menu active classes
    _removeActiveMenuItemClasses() {
        var triMenuItemElement;
        var triMenuItemTopParentElement;
        if (this.currentActiveMenuId != null) {
            triMenuItemElement = document.getElementById(this.currentActiveMenuId);
            if (triMenuItemElement.classList.contains("sideNavMenuItemWithChildren")) {
                triMenuItemElement.parentElement.parentElement.parentElement.classList.remove(ACTIVE_MENU_ITEM_BACKGROUND_CLASS);
                triMenuItemElement.parentElement.parentElement.classList.remove(ACTIVE_MENU_ITEM_BACKGROUND_CLASS);
            } else {
                triMenuItemElement.classList.remove(ACTIVE_MENU_ITEM_BACKGROUND_CLASS);
            }
            triMenuItemElement.classList.remove(ACTIVE_MENU_ITEM_CLASS);
        }
        if (this.currentActiveTopParentMenuId != null) {
            triMenuItemTopParentElement = document.getElementById(this.currentActiveTopParentMenuId);
            if (triMenuItemTopParentElement.classList.contains("sideNavMenuItemWithChildren")) {
                triMenuItemTopParentElement.parentElement.parentElement.parentElement.classList.remove(ACTIVE_TOP_PARENT_MENU_ITEM_BACKGROUND_CLASS);
            }
            triMenuItemTopParentElement.classList.remove(ACTIVE_TOP_PARENT_MENU_ITEM_CLASS);
        }

        //Remove top level descendant classes
        if (this.currentActiveMenuId != null || this.currentActiveTopParentMenuId != null) {
            let topLevelMenuItemId = this.currentActiveTopParentMenuId != null ? this.currentActiveTopParentMenuId: this.currentActiveMenuId;
            let childrenMenuItemIds = this.hierarchyListItemHandler.getChildrenListItemIdsByTopParentId(topLevelMenuItemId);
            //Iterate through each child
            if (childrenMenuItemIds != null) {
                for (var i = 0; i < childrenMenuItemIds.length; i++) {
                    let childMenuItemElement = document.getElementById(childrenMenuItemIds[i]);
                    if (childMenuItemElement != null) {
                        childMenuItemElement.parentElement.parentElement.parentElement.classList.remove(ACTIVE_TOP_PARENT_DESCENDANT_BACKGROUND_CLASS);
                        if (childMenuItemElement.classList.contains("sideNavMenuItemWithChildren")) {
                            childMenuItemElement.parentElement.parentElement.parentElement.classList.remove(ACTIVE_TOP_PARENT_DESCENDANT_BACKGROUND_CLASS);
                        } else {
                            childMenuItemElement.classList.remove(ACTIVE_TOP_PARENT_DESCENDANT_BACKGROUND_CLASS);
                        }
                    }
                }
            }
        }
    }

    resetCurrentActiveIds() {
        this._removeActiveMenuItemClasses();
        this.currentActiveMenuId = null;
        this.currentActiveTopParentMenuId = null;
    }
}