import React, { Component } from "react";
import LandingPage from "../landing/LandingPage.js";
import Sitemap from "../sitemap/Sitemap.js";
import "../nav/NavMain.scss";
import { Application24 } from '@carbon/icons-react';
import { Launch24 } from '@carbon/icons-react';
import MigrateAlt from "@carbon/icons-react/lib/migrate--alt/24";
import { ChevronRight24 } from '@carbon/icons-react';
import { ChevronLeft24 } from '@carbon/icons-react';
import {
  Button,
  PageTitleBar,
  List
} from "tririga-carbon-addons-iot-react";
import "./ProjectContainer.scss";
import { HierarchyListItemHandler } from "../handlers/HierarchyListItemHandler";
import { ActiveProjectSubMenuItemHandler } from "../handlers/ActiveProjectSubMenuItemHandler";
import { NavButtonClickService } from "../services/NavButtonClickService";
import { AppMsg, NavConfig } from "../utils/index.js";

var projectContainerHandler;
//Called from project switch dialog on query renderer.
window.findAccept = function (subCatId, projectId) {
  /* eslint eqeqeq: 0 */
  if (projectId == '') {
    return;
  }
  projectContainerHandler.props.switchProject(SUB_CAT_NOT_NECESSARY, projectId, MENU_ITEM_PROJ_HOME);
}

const COMPANY_LEVEL_PROJECT_ID = 1;
const SUB_CAT_NOT_NECESSARY = -1;
const MENU_ITEM_PROJ_HOME = '';

class ProjectContainer extends Component {

  constructor(props) {
    super(props)
    this.state = {
      currentSelectedMenuItemLabel: '',
      isNavPage: false,
      triNavigationSrc: '',
      currentProjectId: 1,
      currentProjectName: '',
      priorProjectId: 1,
      priorProjectName: '',
      sitemapMenuItemsToDisplay: [],
      sitemapMenuItemIdsToDisplay: [],
      projectMenuItems: [],
      showRecentProject: false,
      isShowProjectTitleBar: false,
      expandedProjectMenuIds: []
    };
    this.projectTitleBarDivRef = React.createRef();
    this.projectSideNavContainerDivRef = React.createRef();
    this.projectDetailsMenuDivRef = React.createRef();
    this.projectSideNavCollapsedDivRef = React.createRef();
    this.landingPageDivRef = React.createRef();
    this.sitemapDivRef = React.createRef();
    this.sitemapRef = React.createRef();
    this.sitemapMenuItemButtonClick = this.sitemapMenuItemButtonClick.bind(this);
    this.handleNavClickComplete = this.handleNavClickComplete.bind(this);
    this.resetQueryProjectSwitcherFunction = this.resetQueryProjectSwitcherFunction.bind(this);
    this.showLandingPage = this.showLandingPage.bind(this);
    this.projectContainerHierarchyListItemHandler = new HierarchyListItemHandler();
    this.navButtonClickService = new NavButtonClickService(this);
    this.shouldHandleSelect = true;
    this.isFromNavClick = false;
    this.activeProjectSubMenuItemHandler = new ActiveProjectSubMenuItemHandler(this.projectContainerHierarchyListItemHandler);
    this.isUpdateFromProjectSubMenuItemToggle = false;
  }

  componentDidMount() {
    //Set the project container handler
    projectContainerHandler = this;
  }

  componentDidUpdate() {
    //Handle active menu item style
    if (this.isUpdateFromProjectSubMenuItemToggle) {
      this.activeProjectSubMenuItemHandler.handle();
      this.isUpdateFromProjectSubMenuItemToggle = false;
    }
  }

  handleProjectState(currentProjectId, currentProjectName, priorProjectId, priorProjectName, showRecentProject) {
    this.setState({
      currentProjectId: currentProjectId,
      currentProjectName: currentProjectName,
      priorProjectId: priorProjectId,
      priorProjectName: priorProjectName,
      showRecentProject: showRecentProject
    });
  }

  handleProjectSubMenuItemTagId(menuItem) {
    menuItem.content.tags = [<div id={this.activeProjectSubMenuItemHandler.generateMenuItemId(menuItem.id)}/>,
                        menuItem.topParentMenuId ? 
                          <div name={this.activeProjectSubMenuItemHandler.generateTopParentMenuItemName(this.activeProjectSubMenuItemHandler.generateMenuItemId(menuItem.topParentMenuId))}/>: 
                        null];
  }

  handleProjectMenuItemsState(projectMenuItems) {
    this.setState({
      projectMenuItems: projectMenuItems,
      isShowProjectTitleBar: true
    });
  }

  handleShowRecentProjectLinkState(showRecentProject) {
    this.setState({
      showRecentProject: showRecentProject
    });
  }

  handleShowProjectTitleBarState(isShowProjectTitleBar) {
    this.setState({
      isShowProjectTitleBar: isShowProjectTitleBar
    });
  }

  selectProjectMenuItem(id, isFromBrowserAction) {
    if (id && this.shouldHandleSelect) {
      //Handle active menu item style
      this.activeProjectSubMenuItemHandler.handle(id);
      this.navButtonClickService.selectProjectMenuItem(id, isFromBrowserAction);
    }
    this.shouldHandleSelect = true;
  }

  _toggleProjectMenuItem(id) {
    if (this.state.expandedProjectMenuIds.filter(rowId => rowId === id).length > 0) {
      this.setState({
        expandedProjectMenuIds: this.state.expandedProjectMenuIds.filter(rowId => rowId !== id)
      });
    } else {
      this.setState({
        expandedProjectMenuIds: this.state.expandedProjectMenuIds.concat([id])
      });
    }
    this.isUpdateFromProjectSubMenuItemToggle = true;
    //We shouldn't handle select on expand/collapse toggle.
    this.shouldHandleSelect = false;
  }

  sitemapMenuItemButtonClick(menuId) {
    if (this.isCurrentProjectAtProjectLevelContext()) {
      //Handle active menu item style
      this.activeProjectSubMenuItemHandler.handle(menuId);
      this.navButtonClick(menuId, this.projectContainerHierarchyListItemHandler);
    } else {
      this.props.sitemapMenuItemButtonClick(menuId);
    }
  }

  navButtonClick(menuId, hierarchyListItemHandler, isFromBrowserAction) {
    this.navButtonClickService.navButtonClick(menuId, hierarchyListItemHandler, isFromBrowserAction);
  }

  handleNavButtonClickState(menuItem, triNavigationSrc, sitemapMenuItemsToDisplay, sitemapMenuItemIdsToDisplay) {
    if (!menuItem.isNavPage) {
      //If on nav page/sitemap style page, nav click boolean should remain false.
      this.isFromNavClick = true;
    }
    this.setState({
      currentSelectedMenuItemLabel: menuItem.content.value,
      triNavigationSrc: triNavigationSrc,
      sitemapMenuItemsToDisplay: sitemapMenuItemsToDisplay,
      sitemapMenuItemIdsToDisplay: sitemapMenuItemIdsToDisplay,
      isNavPage: menuItem.isNavPage
    });
  }

  handleNavFrameSource(menuItemUrl, menuItemName, isSubPageBookmark, isProjectContext) {
    //Validate we are in project context, if we are not coming from sub page bookmark.
    if (!isSubPageBookmark) {
      if (this.isCurrentProjectAtProjectLevelContext()) {
        menuItemUrl = menuItemUrl + '&project=' + this.state.currentProjectId;
        
        //Handle browser history... Note, sub pages already handle history 
        let historyName = '../tririga#name=' + menuItemName;
        if (this.isCurrentProjectAtProjectLevelContext()) {
          historyName = historyName + '&project=' + this.state.currentProjectId;
        }
        var stateObj = { title: menuItemName, url: historyName }; 
        window.history.pushState(stateObj, stateObj.title, stateObj.url);
      }
    }
    //Is from nav click flag. Used by landing page to determine if update is needed.
    this.isFromNavClick = true;
    this.setState({
      isNavPage: false, //Set isNavPage boolean to false, as this will not be a siteMap style page.
      triNavigationSrc: this.props.tririgaContextUrl + menuItemUrl
    });
  }

  handleNavClickComplete() {
    //Production mode handle landing page visibility on triNavigation frame events.
    if (process.env.NODE_ENV==='development') {
      this.showLandingPage();
    }
    //Reset the nav click boolean.
    this.isFromNavClick = false;
  }

  resetQueryProjectSwitcherFunction(triNavFrame) {
    if (process.env.NODE_ENV === 'development') {
      //Just return if development mode. Frame content window changes not supported in development mode.
      return;
    }

    if (triNavFrame.contentWindow && !triNavFrame.contentWindow.changeProject) {
      triNavFrame.contentWindow.changeProject = function (projectId) {
        this.props.switchProject(SUB_CAT_NOT_NECESSARY, projectId, MENU_ITEM_PROJ_HOME);
      }.bind(this);
    }
  }

  _openProjectRecord(projectId) {
    const url = this.props.tririgaContextUrl + '/WebProcess.srv?objectId=750000&actionId=750011&specId=' + projectId;
    window.open(url, "_blank", "toolbar=yes,scrollbars=yes,resizable=yes");
  }

  _openCapitalProjects() {
    const url = this.props.tririgaContextUrl + '/html/en/default/reportTemplate/viewPageReport.jsp?reportTemplId='
      + NavConfig.getNavConfigData().projectPickerQueryId +
      '&selectType=single&manager=1&projectSearch=1&parentSOSubCategoryId=2&showAdd=0';
    window.open(url, "_blank", "toolbar=yes,scrollbars=yes,resizable=yes");
  }

  _switchToRecentProject = (projectId) => {
    console.log('Switch To recent Project')
    this.props.switchProject(SUB_CAT_NOT_NECESSARY, projectId, MENU_ITEM_PROJ_HOME);
  }

  isCurrentProjectAtProjectLevelContext() {
    return this.isProjectLevelContext(this.state.currentProjectId);
  }

  isProjectLevelContext(projectId) {
    return projectId > COMPANY_LEVEL_PROJECT_ID;
  }

  _expandProjectSideNav() {
    this.projectDetailsMenuDivRef.current.classList.add("show");
    this.projectSideNavCollapsedDivRef.current.style.display = "none";
  }

  _collapseProjectSideNav() {
    this.projectDetailsMenuDivRef.current.classList.remove("show");
    this.projectSideNavCollapsedDivRef.current.style.display = "block";
  }

  _handleProjectTitleBarDisplayStyle() {
    if (this.state.isShowProjectTitleBar) {
      return '';
    }
    return 'none';
  }

  _handleProjectSideNavContainerDisplayStyle() {
    if (this.isCurrentProjectAtProjectLevelContext()) {
      return 'flex';
    }
    return 'none';
  }

  //Adding css class which changes the landing page behaviour below medium Breakpoint.
  _mediumBreakpointFunction() {
    if (this._handleProjectSideNavContainerDisplayStyle() === 'flex') {
      return (NavConfig.getNavConfigData().isRTL) ? 'project-landingpage-rtl' : 'project-landingpage';
    }
    else {
      return '';
    }
  }

  _handleSitemapDisplayStyle() {
    if (this.state.isNavPage) {
      return 'flex';
    }
    return 'none';
  }

  showLandingPage() {
    this.landingPageDivRef.current.style.display = 'flex';
  }

  hideLandingPageAndSitemap() {
    this.hideLandingPage();
    this.sitemapDivRef.current.style.display = 'none';
  }

  hideLandingPage() {
    this.landingPageDivRef.current.style.display = 'none';
  }

  render() {
    let projectMainHeight = (this._handleProjectTitleBarDisplayStyle() === 'none') ? '100%' : '90.3%';
    let projectTitleBarClassName = NavConfig.getNavConfigData().isCompanyLevelAccessAllowed ? 'projects-switch-page-title' : 'projects-switch-page-title-no-company-access';
    projectTitleBarClassName = NavConfig.getNavConfigData().isRTL ? projectTitleBarClassName + '-rtl' : projectTitleBarClassName;
    let projectSideNavContainerCSS = NavConfig.getNavConfigData().isRTL ? 'projectSideNavContainer-rtl' : 'projectSideNavContainer';
    return (
      <div style={{ height: 'inherit' , paddingLeft: '32px', paddingRight: '32px', paddingTop: this.state.isShowProjectTitleBar ? '0px' : '16px' }}>
        <div ref={this.projectTitleBarDivRef} style={{ display: this._handleProjectTitleBarDisplayStyle() }}>
          <PageTitleBar
            className={projectTitleBarClassName}
            collapsed={false}
            content={undefined}
            description={null}
            editable={false}
            extraContent={
              <>
                {/*  To display Recent project Start */}
                {this.state.showRecentProject ?
                  <span>
                    <span>
                      {AppMsg.getMessage(AppMsg.PROJ_RECENT)}
                    </span>
                    <span className='recentProjectButton'>
                      <Button renderIcon={MigrateAlt}
                        kind="ghost"
                        onClick={() => { this._switchToRecentProject(this.state.priorProjectId) }}
                      >
                        {this.state.priorProjectName}
                      </Button>
                    </span>
                  </span>
                  : null}
                {/*  To display Recent project End */}
                {this.isCurrentProjectAtProjectLevelContext() ?
                  <div className="openProjectRecord">
                    <Button renderIcon={Launch24}
                      kind="ghost"
                      onClick={() => { this._openProjectRecord(this.state.currentProjectId) }}
                    >
                      {AppMsg.getMessage(AppMsg.PROJ_RECORD)}
                    </Button>
                  </div>
                  : null} {/*openProjectRecord*/}
                <div className="projectActionsContainer">
                  <Button renderIcon={Application24}
                    kind="ghost"
                    onClick={() => { this._openCapitalProjects() }}
                  >
                    {AppMsg.getMessage(AppMsg.PROJ_SWITCH)}
                  </Button>
                </div> {/*projectActionsContainer*/}
              </>
            }
            isLoading={false}
            onEdit={null}
            tabs={undefined}
            title={this.state.currentProjectName}
          />
        </div>

        <main className="app" style={{ height: projectMainHeight }}>
          <section className="main">
            {
              <div style={{ height: 'inherit' }}>
                <div className={projectSideNavContainerCSS}
                  ref={this.projectSideNavContainerDivRef}
                  //Subtracted 105 to stop the project side nav to move below screen size
                  style={{ minHeight: document.body.clientHeight - 105, height: '100%', display: this._handleProjectSideNavContainerDisplayStyle() }}>

                  <div id="projectSideNavCollapsed" ref={this.projectSideNavCollapsedDivRef} className="project-side-nav-menu-collapsed" onClick={() => { this._expandProjectSideNav() }}>
                    <Button
                      renderIcon={NavConfig.getNavConfigData().isRTL ? ChevronLeft24 : ChevronRight24}
                      iconDescription={AppMsg.getMessage(AppMsg.PROJ_MENU)}
                      aria-expanded={false}
                      hasIconOnly
                      kind="ghost"
                      className="project-panel-expand"
                      onClick={() => { this._expandProjectSideNav() }}
                      size="default"
                      tooltipAlignment="center"
                      tooltipPosition={NavConfig.getNavConfigData().isRTL ? 'left' : 'right'}
                    >
                    </Button>
                  </div>
                  <div id="projectDetailsMenu" ref={this.projectDetailsMenuDivRef} className="project-side-nav-menu">
                    <List
                      buttons={[                    
                          <Button
                          renderIcon={NavConfig.getNavConfigData().isRTL ? ChevronRight24 : ChevronLeft24}
                          iconDescription={AppMsg.getMessage(AppMsg.PROJ_MENU)}
                          aria-expanded={true}
                          hasIconOnly
                          kind="ghost"
                          className="project-panel-expand"
                          onClick={() => { this._collapseProjectSideNav() }}
                          size="default"
                          tooltipAlignment="center"
                          tooltipPosition={NavConfig.getNavConfigData().isRTL ? 'left' : 'right'}
                        >
                        </Button>
                      ]
                    }
                      hasMultiSelect={false}
                      hasPagination={false}
                      hasSearch
                      i18n={{
                        close: AppMsg.getMessage("EXPAND"),
                        expand: AppMsg.getMessage("CLOSE"),
                        searchPlaceHolderText: AppMsg.getMessage(AppMsg.PROJ_SEARCH)
                      }}
                      isFullHeight={false}
                      isLoading={false}
                      items={this.state.projectMenuItems}
                      handleSelect={(id) => { this.selectProjectMenuItem(id) }}
                      title={AppMsg.getMessage(AppMsg.PROJ_DETAILS)}
                      expandedIds={this.state.expandedProjectMenuIds}
                      toggleExpansion={(id) => {
                        this._toggleProjectMenuItem(id);
                      }}
                    />
                  </div>
                </div>
                {/*Main landing page */}
                  <div style={{ height: 'inherit' }}>
                    <div ref={this.landingPageDivRef} className={this._mediumBreakpointFunction()} style={{ height: 'inherit' }}>
                      <LandingPage
                        resetQueryProjectSwitcherFunction={this.resetQueryProjectSwitcherFunction}
                        triNavigationSrc={this.state.triNavigationSrc}
                        isFromNavClick={this.isFromNavClick}
                        handleNavClickComplete={this.handleNavClickComplete}
                        // To change the size of Landing Page for Company Level
                        isCompanyLevelAccessAllowedStyle={this._handleProjectSideNavContainerDisplayStyle()}
                        isShowProjectTitleBar={this.state.isShowProjectTitleBar}
                        isRTL={NavConfig.getNavConfigData().isRTL}
                        showLandingPage={this.showLandingPage}
                      />
                    </div>
                    <div ref={this.sitemapDivRef} className="sitemapDiv" style={{ display: this._handleSitemapDisplayStyle(), height: '98%'  }}>
                      <Sitemap ref={this.sitemapRef}
                        {...this.state}
                        sitemapMenuItemButtonClick={this.sitemapMenuItemButtonClick}
                      />
                    </div>
                  </div>
                </div>
            }
          </section>
        </main>
      </div>
    );
  }
}

export default ProjectContainer;

